*{
    background: rgb(168, 164, 235);
}
.heading{
    text-align: center;
    text-decoration: underline;
    font-family: 'Rubik Pixels', cursive;
}
.container{
    margin: auto;
    width: 50%;
    display: block;
    height: 90%;
    padding-left: 30px;
    padding-top: 30px;
    padding-bottom: 30px;
    border: 3px solid green;
}
#btn1{
    background-color: white; 
    color: black; 
    border: 2px solid #e12a2a;
    padding: 10px 20px;
    transition-duration: 0.4s;
    cursor: pointer;
    border-radius: 10%;

}
#btn1:hover {
    background-color: #e52020; /* Green */
    color: rgb(245, 244, 247);
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
  }
#input1-box{
    margin-left: auto;
    margin-right: 20px;
    padding: 10px 20px;
    color: rgb(239, 12, 12);
    font-size: medium;
    background-color: white;
    
}

